













export default {

	props: {
		percent: {
		
			default: 0
		},
		// normal || active || wrong || success
		status: {
			
			default: 'normal'
		},
		strokeWidth: {
			
			default: 20
		},
		hideInfo: {
			
			default: false
		}
	},computed: {
		progressStatus() {
			return 'i-progress-'+ this.status;
		},
		height(){
			var px=uni.upx2px(this.strokeWidth)
			return px+"px"
		},
		width(){
			return this.percent+"%"
		}
		
	},
}
